#TodoHistory .dailyTodos {
    padding: 16px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 8px;
    display: -ms-flexbox;
    display: flex;
  }
    #TodoHistory .dailyTodos .summary {
      width: 100px;
      margin-right: 8px;
    }
    #TodoHistory .dailyTodos .summary .date span:first-child {
          margin-right: 4px;
          color: #222222;
        }
    #TodoHistory .dailyTodos .summary .date span:last-child {
          color: #888;
          font-size: 12px;
        }
    #TodoHistory .dailyTodos .summary .finishedCount {
        font-size: 14px;
        color: #888;
      }
    #TodoHistory .dailyTodos .todoList {
      -ms-flex: 1 1;
          flex: 1 1;
    }



