#Home {
  margin: 0 auto;
  max-width: 1000px;
  min-width: 800px;
}
  #Home > header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 8px 4px;
    border-bottom: 1px solid #ddd;
  }
  #Home > header .logo {
      font-size: 20px;
      font-weight: 700;
    }
  #Home > header .user-setting > i {
      margin-left: 8px;
    }
  #Home > main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }



