#TomatoList {
  margin-top: 16px;
  border-top: 1px solid #ddd;
}
  #TomatoList .dailyTomatoes {
    padding-top: 16px;
  }
  #TomatoList .dailyTomatoes .title {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-align: center;
          align-items: center;
      margin-bottom: 4px;
    }
  #TomatoList .dailyTomatoes .title .dateTime {
        color: #999;
        font-size: 16px;
      }
  #TomatoList .dailyTomatoes .title .finishedCount {
        color: #666;
        font-size: 12px;
      }
  #TomatoList .dailyTomatoes .TomatoItem {
      margin: 4px 0;
      font-size: 14px;
    }
  #TomatoList .dailyTomatoes .TomatoItem .timeRange {
        color: #999999;
      }
  #TomatoList .dailyTomatoes .TomatoItem .description {
        color: #222222;
        margin-left: 8px;
      }



