#Statistics {
  margin: 8px;
}
  #Statistics ul {
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    height: 96px;
    border: 1px solid #ddd;
    border-radius: 8px;
    list-style: none;
  }
  #Statistics ul li {
      -ms-flex: 1 1;
          flex: 1 1;
      position: relative;
      padding: 8px;
      cursor: pointer;
      // border: 1px solid #e4e4e4;
      color: #666;
      -ms-flex-pack: center;
          justify-content: center;
    }
  #Statistics ul li .class {
        position: absolute;
        top: 0;
        left: 8px;
        text-align: left;
        margin: 4px 0;
      }
  #Statistics ul li .class .title {
          font-size: 16px;
          margin: 4px 0;
        }
  #Statistics ul li .class .subtitle {
          color: #ccc;
          font-size: 12px;
          margin: 4px 0;
        }
  #Statistics ul li .class .count {
          font-size: 22px;
          margin: 4px 0;
        }
  #Statistics ul li .polygon {
        position: absolute;
        bottom: 8px;
        right: 8px;
        margin: 4px;
        width: 200px;
        height: 60px;
      }
  #Statistics ul li .polygon svg {
          overflow: hidden;
          width: 200px;
          height: 60px;
        }
  #Statistics ul li:first-child {
      border-right: 1px solid #ddd;
    }
  #Statistics ul li:last-child {
      border-left: 1px solid #ddd;
    }



