#TodoHistoryTodoItem {
  padding: 8px;
  font-size: 14px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
  #TodoHistoryTodoItem .text .time {
      color: #999;
      margin-right: 8px;
    }
  #TodoHistoryTodoItem .text .description {
      color: #222222;
    }
  #TodoHistoryTodoItem .action {
    display: none;
  }
  #TodoHistoryTodoItem .action span {
      color: deepskyblue;
      cursor: pointer
    }
  #TodoHistoryTodoItem .action span:first-child {
  margin-right: 4px;
}
#TodoHistoryTodoItem:hover {
  background: #f4f4f4;
}
#TodoHistoryTodoItem:hover > .action {
      display: block;
}



